<template>
  <!--
      :settings="{packages: []}"
      :data=chartData()
      :options=chartOptions()
  -->
  <GChart
    :resizeDebounce="500"
    :settings="{packages: ['bar']}"
    :data="chartData"
    :options="chartOptions"
    :createChart="(el, google) => new google.charts.Bar(el)"
     @ready="onChartReady"
  />
</template>
<script>
export default {
  name: 'bar-chart',
  //  props
  //  chartData:"message"
  props: {
    chartData: {
      default: ''
    },
    chartColor: {
      default: ''
    },
    chartHeight: {
      default: ''
    },
    chartWidth: {
      default: ''
    },
    chartTitle: {
      default: ''
    },
    chartSubTitle: {
      default: ''
    }
  },
  data () {
    return {
      chartsLib: null
    }
  },
  computed: {
    chartOptions () {
      //  nullnull
      if (!this.chartsLib) {
        return null
      }
      //  
      return this.chartsLib.charts.Bar.convertOptions({
        chart: {
          //  
          title: this.chartTitle,
          //  
          subtitle: this.chartSubTitle
        },
        bars: 'horizontal', //   Required for Material Bar Charts.
        hAxis: {
          minValue: 0
        },
        //  
        height: this.chartHeight,
        // width: this.chartWidth,
        //  [[''],[''],['']]
        colors: this.chartColor
      }
      )
    }
  },
  methods: {
    onChartReady (chart, google) {
      this.chartsLib = google
    }
  }
}
</script>
